import React, { useState, useEffect } from 'react';
import { View, Text, TextInput, Button, FlatList, TouchableOpacity, Alert } from 'react-native';
import { auth, db } from './firebaseConfig';
import Swal from 'sweetalert2';

import { 
  signInWithEmailAndPassword, 
  createUserWithEmailAndPassword, 
  onAuthStateChanged, 
  signOut 
} from 'firebase/auth';
import { 
  collection, 
  doc, 
  setDoc, 
  getDoc, 
  onSnapshot, 
  query, 
  orderBy, 
  getDocs, 
  addDoc 
} from 'firebase/firestore';

const App = () => {
  const [user, setUser] = useState(null);
  const [users, setUsers] = useState([]);
  const [selectedChatUser, setSelectedChatUser] = useState(null);
  const [message, setMessage] = useState('');
  const [messages, setMessages] = useState([]);
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const validateEmail = (email) => {
    // Basit bir e-posta dorulama regex'i
    const re = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    return re.test(String(email).toLowerCase());
  };
  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (currentUser) => {
      setUser(currentUser);
      if (currentUser) {
        loadUsers();
      }
    });

    return () => unsubscribe();
  }, []);

  const loadUsers = async () => {
    const usersSnapshot = await getDocs(collection(db, 'users'));
    const usersList = usersSnapshot.docs.map(doc => ({
      uid: doc.id,
      ...doc.data()
    })).filter(u => u.uid !== user?.uid);
    setUsers(usersList);
  };

const handleLogin = async () => {
  if (!validateEmail(email)) {
    Swal.fire({
      title: 'Geersiz E-posta',
      text: 'Ltfen geerli bir e-posta adresi girin. rnein: ornek@ornek.com',
      icon: 'error',
    });
    return;
  }

  // ifre uzunluu kontrol
  if (password.length < 6) {
    Swal.fire({
      title: 'Geersiz ifre',
      text: 'ifreniz en az 6 karakter olmaldr.',
      icon: 'error',
    });
    return;
  }

  try {
    // Firebase giri ilemi (validasyonlar geildiinde)
    // ...
  } catch (error) {
    // Hata yakalama ve zelletirilmi hata mesaj
    // ...
  }
  try {
    const userCredential = await signInWithEmailAndPassword(auth, email, password);
    const userDoc = await getDoc(doc(db, 'users', userCredential.user.uid));

    if (userDoc.exists() && !userDoc.data().approved) {
      await signOut(auth);
      Swal.fire({
        title: 'Onay Bekleniyor',
        text: 'Hesabnz henz onaylanmad. Ltfen ynetici onayn bekleyin.',
        icon: 'warning',
      });
    } else {
      Swal.fire({
        title: 'Giri Baarl!',
        text: 'Baaryla giri yaptnz.',
        icon: 'success',
      });
    }
  } catch (error) {
    let errorMessage = '';

    // Firebase hata kodlarna gre zelletirilmi mesajlar
    switch (error.code) {
      case 'auth/user-not-found':
        errorMessage = 'Bu e-posta adresi ile kaytl bir kullanc bulunamad.';
        break;
      case 'auth/wrong-password':
        errorMessage = 'Girdiiniz ifre yanl. Ltfen tekrar deneyin.';
        break;
      case 'auth/invalid-email':
        errorMessage = 'Geersiz e-posta adresi girdiniz. Ltfen kontrol edin.';
        break;
      case 'auth/user-disabled':
        errorMessage = 'Bu kullanc hesab devre d braklm.';
        break;
      default:
        errorMessage = 'Giri srasnda bir hata olutu. Ltfen tekrar deneyin.';
        break;
    }

    Swal.fire({
      title: 'Giri Hatas',
      text: errorMessage,
      icon: 'error',
    });
  }
};

const handleSignUp = async () => {
  if (!validateEmail(email)) {
    Swal.fire({
      title: 'Geersiz E-posta',
      text: 'Ltfen geerli bir e-posta adresi girin. rnein: ornek@ornek.com',
      icon: 'error',
    });
    return;
  }

  // ifre uzunluu kontrol
  if (password.length < 6) {
    Swal.fire({
      title: 'Geersiz ifre',
      text: 'ifreniz en az 6 karakter olmaldr.',
      icon: 'error',
    });
    return;
  }

  try {
    // Firebase kayt ilemi (validasyonlar geildiinde)
    // ...
  } catch (error) {
    // Hata yakalama ve zelletirilmi hata mesaj
    // ...
  }
  try {
    const userCredential = await createUserWithEmailAndPassword(auth, email, password);
    const newUser = userCredential.user;
    await setDoc(doc(db, 'users', newUser.uid), {
      email: newUser.email,
      approved: false
    });

    await signOut(auth);

    Swal.fire({
      title: 'Kayt Baarl',
      text: 'Kayt ileminiz baarl. Hesabnz onaylandktan sonra giri yapabileceksiniz.',
      icon: 'success',
    });
  } catch (error) {
    let errorMessage = '';

    // Firebase hata kodlarna gre zelletirilmi mesajlar
    switch (error.code) {
      case 'auth/email-already-in-use':
        errorMessage = 'Bu e-posta adresi zaten kullanmda. Farkl bir e-posta deneyin.';
        break;
      case 'auth/weak-password':
        errorMessage = 'ifreniz ok zayf. Ltfen daha gl bir ifre sein.';
        break;
      case 'auth/invalid-email':
        errorMessage = 'Geersiz e-posta adresi girdiniz. Ltfen kontrol edin.';
        break;
      default:
        errorMessage = 'Kayt srasnda bir hata olutu. Ltfen tekrar deneyin.';
        break;
    }

    Swal.fire({
      title: 'Kayt Hatas',
      text: errorMessage,
      icon: 'error',
    });
  }
};

  const createChatRoom = async (selectedUser) => {
    const chatRoomId = user?.uid > selectedUser.uid
      ? `${user.uid}_${selectedUser.uid}`
      : `${selectedUser.uid}_${user?.uid}`;

    setSelectedChatUser(selectedUser);

    const q = query(
      collection(db, 'chatRooms', chatRoomId, 'messages'),
      orderBy('createdAt', 'desc')
    );

    onSnapshot(q, (querySnapshot) => {
      const messagesList = querySnapshot.docs.map(doc => ({
        id: doc.id,
        ...doc.data()
      }));
      setMessages(messagesList);
    });
  };

  const handleSendMessage = async () => {
    if (message.length > 0 && selectedChatUser) {
      const chatRoomId = user?.uid > selectedChatUser.uid
        ? `${user.uid}_${selectedChatUser.uid}`
        : `${selectedChatUser.uid}_${user?.uid}`;

      await addDoc(collection(db, 'chatRooms', chatRoomId, 'messages'), {
        text: message,
        createdAt: new Date(),
        user: user?.email
      });
      setMessage('');
    }
  };

  if (!user) {
    return (
      <View style={{ padding: 20 }}>
        <Text>Email:</Text>
        <TextInput
          value={email}
          onChangeText={setEmail}
          style={{ borderWidth: 1, marginBottom: 10, padding: 5 }}
        />
        <Text>Password:</Text>
        <TextInput
          value={password}
          onChangeText={setPassword}
          secureTextEntry
          style={{ borderWidth: 1, marginBottom: 10, padding: 5 }}
        />
        <Button title="Login" onPress={handleLogin} />
        <Button title="Sign Up" onPress={handleSignUp} />
      </View>
    );
  }

  if (!selectedChatUser) {
    return (
      <View style={{ flex: 1, padding: 20 }}>
        <Text>Kullanclar</Text>
        <FlatList
          data={users}
          keyExtractor={(item) => item.uid}
          renderItem={({ item }) => (
            <TouchableOpacity onPress={() => createChatRoom(item)}>
              <View style={{ padding: 10, backgroundColor: '#f1f1f1', marginVertical: 5 }}>
                <Text>{item.email}</Text>
              </View>
            </TouchableOpacity>
          )}
        />
        <Button title="k Yap" onPress={() => signOut(auth)} />
      </View>
    );
  }

  return (
    <View style={{ flex: 1, padding: 20 }}>
      <Text>{selectedChatUser?.email} ile sohbet ediyorsunuz</Text>
      <FlatList
        data={messages}
        keyExtractor={(item) => item.id}
        renderItem={({ item }) => (
          <View style={{ padding: 10, backgroundColor: '#f1f1f1', marginVertical: 5 }}>
            <Text>{item.user}: {item.text}</Text>
          </View>
        )}
        inverted
      />
      <TextInput
        value={message}
        onChangeText={setMessage}
        placeholder="Mesaj yazn"
        style={{ borderWidth: 1, padding: 10, marginBottom: 10 }}
      />
      <Button title="Gnder" onPress={handleSendMessage} />
      <Button title="Geri Dn" onPress={() => setSelectedChatUser(null)} />
      <Button title="k Yap" onPress={() => signOut(auth)} />
    </View>
  );
};

export default App;
